import { useTranslation } from "react-i18next";
import { assestsUserFinanceUrl } from "../../../Services/UrlConfig/AssestsBaseUrl";
import BorderBottom from "../../../Ui/Border/BorderBottom";
import Modal from "../../../Ui/Modal/Modal";
import UserMemberContentWrapper from "../../../Ui/UserMember/UserMemberContentWrapper";
import styleIncome from "../../FinanceManager/Income/Income.module.css";
import CreateApplicationType from "./CreateApplicationType";
import { useState } from "react";
import CreateApplicationStepWise from "./CreateApplicationStepWise";
import CreateDirectApplication from "./CreateDirectApplication/CreateDirectApplication";
const CreateApplicationConfig = ({
  onClose,
  onRefetch,
  aid,
  fid,
  instituteId,
}) => {
  const { t } = useTranslation();
  const [selectedApplicationType, setSelectedApplicationType] = useState("");
  const onSelectType = (value) => {
    setSelectedApplicationType(value);
  };

  // console.info("selectedApplicationType", selectedApplicationType);
  return (
    <>
      {!selectedApplicationType ? (
        <Modal>
          <div className={styleIncome.modal_container}>
            <UserMemberContentWrapper>
              <div className={styleIncome.modal_container_outer_header}>
                <div className={styleIncome.modal_container_header}>
                  <h6>{t("select_admission_application_type")}</h6>
                </div>
                <img
                  src={`${assestsUserFinanceUrl}/close.svg`}
                  alt="close icon"
                  onClick={onClose}
                />
              </div>
            </UserMemberContentWrapper>
            <BorderBottom />
            <UserMemberContentWrapper
              customStyle={{
                paddingTop: "0",
                marginBottom: "1rem",
              }}
            >
              <CreateApplicationType onSelectType={onSelectType} />
            </UserMemberContentWrapper>
          </div>
        </Modal>
      ) : selectedApplicationType === "Complete_Step_Wise_Admission" ? (
        <CreateApplicationStepWise
          onClose={() => setSelectedApplicationType("")}
          aid={aid}
          onRefetch={onRefetch}
          instituteId={instituteId}
          fid={fid}
          onParentClose={onClose}
        />
      ) : selectedApplicationType === "Direct_Admission" ? (
        <CreateDirectApplication
          onClose={() => setSelectedApplicationType("")}
          aid={aid}
          onRefetch={onRefetch}
          instituteId={instituteId}
          fid={fid}
          onParentClose={onClose}
        />
      ) : null}
    </>
  );
};

export default CreateApplicationConfig;
